main {
    position: absolute;
    right: 0;
    left: 30rem;
    border: 0;
    top: 0;
    overflow: auto;
    height: 100%;
}

subnav {
    height: 100%;
    background-color: #f2f2f2;
    position: fixed;
    width: 10%;
    width: 24rem;
    top: 0;
    bottom: 0;
    left: 6rem;
    right: 0;
    overflow-y: scroll;
    overflow-x: hidden;

    .subnav_title {
        display: none;
    }

    .icon_close {
        font-size: 3rem;
        position: absolute;
        right: 20px;
        top: 20px;

        &:hover {
            cursor: pointer;
        }
    }

    .subnav_head {
        display: none;
    }
}

img {
    height: 100%;
    width: 100%;
}

.container {
    display: block;
    margin: 0 auto;
    max-width: 60rem;
    width: 100%;
    padding: 1px;
    position: relative;
}
@media(max-width: 1200px) {
    subnav {
        left: 0;

        .subnav_title {
            display: block;
        }

        .pad-head {
            margin-top: 6rem;
        }
    }

    main {
        left: 24rem;
    }
}
@media(max-width: 1000px) {
    subnav {
        left: 0;
        width: 20rem;
    }

    main {
        left: 20rem;
    }
}
@media(max-width: 830px) {
    subnav {
        display: block;
        position: absolute;
        z-index: 3;
        margin: 0 auto;
        text-align: center;
        width: 100%;
        //transform: translate3d(0, -100%, 0);
        .subnav_head {
            display: block;
        }

        .card {
            margin: 0 auto;
        }

        .pad-head {
            margin-top: 0;
        }
    }

    main {
        left: 0;
    }
}

.home_content {
    position: fixed;
    right: 0;
    left: 6rem;
    border: 0;
    top: 0;
    overflow: auto;
    height: 100%;
    background: #101010;

    padding: 10rem 10rem 0;
    h1 {
        color: white;
        font-size: 6rem;
        text-align: left;
    }
    p {
        color: white;
        padding: 0px 20px;
    }

    @media(max-width: 1200px) {
        left: 0;
        text-align: center;
        h1 {
            text-align: center;
        }
    }

        @media(max-width: 1200px) {
            padding: 0;
                padding-top: 10rem;
                h1 {
                font-size:    3.5rem;
                }
        }
}
