body, button {
    font-family: 'Nunito', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Montserrat', sans-serif;
}

h1 {
    text-align: center;

    &.huge {
        font-size: 5rem;
    }

    @media(max-width: 470px) {
        &.huge {
            font-size: 3.2rem;
        }
    }
}
