.card {
    padding: 1rem 2rem 2rem;
    margin: 1rem 0;
    max-width: 320px;
    max-height: 300px;

    .project_picture {
        height: 13rem;
        background: black;
        box-shadow: 0;
        transition: background 200ms ease-in;
        transition: box-shadow 180ms ease-in-out;

        &:hover {
            cursor: pointer;
            //box-shadow: rgba(100, 76, 76, 0.2) 0 2px 2px, rgba(100, 76, 76, 0.2) 0 4px 4px, rgba(100, 76, 76, 0.2) 0 8px 8px, rgba(100, 76, 76, 0.2) 0 16px 16px, rgba(100, 76, 76, 0.2) 0 32px 32px, rgba(100, 76, 76, 0.2) 0 64px 64px;
            box-shadow: rgba(172, 172, 180, 0.2) 0 2px 2px, rgba(172, 172, 180, 0.2) 0 4px 4px, rgba(172, 172, 180, 0.2) 0 8px 8px, rgba(172, 172, 180, 0.2) 0 16px 16px, rgba(172, 172, 180, 0.2) 0 32px 32px, rgba(172, 172, 180, 0.2) 0 64px 64px;
        }

        &.active {
            cursor: pointer;
            //box-shadow: rgba(100, 76, 76, 0.2) 0 2px 2px, rgba(100, 76, 76, 0.2) 0 4px 4px, rgba(100, 76, 76, 0.2) 0 8px 8px, rgba(100, 76, 76, 0.2) 0 16px 16px, rgba(100, 76, 76, 0.2) 0 32px 32px, rgba(100, 76, 76, 0.2) 0 64px 64px;
            box-shadow: rgba(80, 80, 80, 0.2) 0 2px 2px, rgba(102, 102, 102, 0.2) 0 4px 4px, rgba(100, 76, 76, 0.2) 0 8px 8px, rgba(99, 99, 99, 0.2) 0 16px 16px, rgba(90, 90, 90, 0.2) 0 32px 32px, rgba(104, 104, 104, 0.2) 0 64px 64px;
        }

        &.is_loading {
            img {
                opacity: 0;
            }
        }

        img {
            opacity: 1;
            height: 13rem;
            transition: opacity .4s ease-in;
        }

        @media(max-width: 768px) {
                height: 200px;
                img {
                        height: 217px;
                }
        }

        @media(max-width: 320px) {
                height: 180px;
                img {
                        height: 180px;
                }
        }
    }

    .project_name {
        margin-top: 20px;

        h4 {
            color: #383838;
            margin: 0;
        }

        p {
            font-size: 0.99rem;
            color: #383838;
            padding: 0;
            margin: 10px 0 0;
        }
    }
}
