.content {
    //margin: 7rem 12rem;
    //height: 50rem;
    background: white;
    color: #2f2f2f;

    &.dark {
        background: #090909;
        color: white;
    }

    .header {
        position: relative;
        background: #262626;

        .bg {
            background-color: #262626;
            background-position: center center;
            background-attachment: fixed;
            background-size: cover;
            background-position-y: 0;
            text-align: center;
            height: 30rem;
            width: 100%;
            transition: opacity 0.4s ease-in;
            opacity: 0;
            background-repeat: no-repeat;

            &.is_loaded {
                opacity: 1;
            }
            @media(max-width: 768px) {
                height: 22rem;
            }
        }

        .name {
            position: absolute;
            background: rgba(0, 0, 0, 0.7);

            &.low {
                background: rgba(0, 0, 0, 0.4);
            }
            width: 100%;
            height: 100%;
            top: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            h1 {
                color: white;
            }
        }
    }

    .description {
        padding: 0 3rem;
        margin: 70px auto 0;

        .text {
            max-width: 40rem;
            margin: 0 auto;
        }

        .image_desc {
            text-align: center;
            color: #454545;
            margin: 0 auto;
            display: block;
            font-weight: 100;

            &.dark {
                color: #7f7f7f;
                margin-bottom: 50px;
            }
        }
    }

    .next_project {
        // height: 10rem;
        background: black;
        padding: 0 2.9rem;
        color: white;
        // padding: 0 15rem;
        // text-align: center;
        h1,
        h3 {
            text-align: left;
            color: white;
        }

        h3 {
            margin: 20px 0;
            font-family: 'Nunito', sans-serif;
            font-weight: 100;
            display: inline-block;
            padding-bottom: 10px;
            margin: 10px 0;
            position: relative;

            &:after {
                content: ' ';
                height: 2px;
                width: 100%;
                background: white;
                position: absolute;
                left: 0;
                bottom: 1px;
                opacity: 0;
                transition: opacity 150ms ease-in;
            }
        }

        h1 {
            display: block;
            margin-top: 0;
        }

        &.light {
            background: white;
            color: black;

            h1,
            h3 {
                text-align: left;
                color: black;

                &:after {
                    background: black;
                }
            }
        }
    }

    .next_project_link {
        &:hover {
            h3::after {
                opacity: 1;
            }
        }
    }
}

.social_links {
    position: fixed;
margin: 0 auto;
text-align: center;
bottom: 2rem;
display: none;
@media(max-width: 1200px) {
    display: block;
}
    ul {
        list-style: none;
        margin: 0 auto;
        padding: 0;


        li {
            display: inline-block;
            padding: 20px;
            .fa {
                color: white;
                font-size: 2rem;
            }
        }
    }
}
