nav {
    height: 100%;
    background-color: white;
    position: fixed;
    width: 10%;
    width: 6rem;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;


    img {
        height: auto;
    }

    .logo {
        height: 49px;
        width: 46px;
        margin: 20px auto;
        text-align: center;
    }

    ul {
        list-style: none;
        margin: 0 auto;
        padding: 0;

        li {
            letter-spacing: 2px;
        }

        &.nav {
            margin-top: 9rem;
            @media(max-width: 1440px) {
                margin-top: 2rem;
            }

            li {
                padding: 6rem 0;
                transform: rotate(-90deg);
                text-align: center;
                height: 23px;
                width: 20px;
                font-size: 1rem;
                &:hover {
                    cursor: pointer;
                }
            }

            &.nav-dt li {

                &::after {
                    content: '';
                    height: 4px;
                    width: 100%;
                    background: black;
                    position: absolute;
                    left: 0;
                    margin-top: 5px;
                    opacity: 0;
                }

                a {
                    color: black;

                    &:focus,
                    &:hover {
                        color: black;
                    }
                }

                &.active,
                &:hover {
                    &::after {
                        transition: height 200ms ease-in;
                        opacity: 1;
                    }
                }
            }
        }



        &.nav-mb {
            display: none;
        }
        //hide dt
        @media(max-width: 830px) {
            &.nav-dt {
                display: none;
            }
            &.nav-mb {
                display: block;
            }
        }

        &.social {
            @media(max-height: 726px) {
                display: none;
            }
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;

            li {
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;
            }
        }
    }
    @media(max-width: 1200px) {
        position: absolute;
        z-index: 3;
        width: 100%;
        height: 64px;
        flex-direction: row;
        align-items: center;
        box-shadow: 0 2px 6px -2px rgba(0,0,0,.25);
        .logo {
            margin: 5px 20px;
        }

        .social {
            display: none;
        }

        ul.nav {
            margin: 0 0 0 auto;

            li {
                padding: 0 10px;
                transform: rotate(0);
                display: inline;
                position: relative;
                text-align: center;
                margin: 0 10px;

                &::after {
                    margin: 0;
                    bottom: -21px;
                    height: 10px;
                }
            }



            &.nav-dt li::after {
                top: 2.1rem;
            }
        }
    }

    @media(max-width: 320px) {

        .logo{
                margin: 20px auto;
        }
        ul.nav li {
            padding: 0px;
        }
    }
}
