//colors
$primary-color: #FF8300;

body,
html {
    font-size: 16px;
    display: flex;
}
@media(max-width: 1680px) and (max-height: 1050px) {
    html {
        font-size: 14px;
    }
}
@media(max-width: 1440px) and (max-height: 900px) {
    html {
        font-size: 12px;
    }
}

body {
    background-color: white;
    margin: 0 auto;
}

p {
    font-style: normal;
    font-size: 1.165rem;
    line-height: 1.58;
}

.btn {
    border: 1px solid white;
    background: transparent;
    border-radius: 50px;
    color: white;
    font-size: 1.2rem;
    padding: 15px 50px;
    text-decoration: none;
    transition: all 120ms ease-in-out;

    &:hover {
        background: white;
        color: black;
        text-decoration: none;
        border-color: black;
        cursor: pointer;
    }
}

.resume_btn {
    z-index: 1;
    position: fixed;
    bottom: 30px;
    right: 20px;
    background: white;
    border: 0;
    border-radius: 50%;
    height: 5rem;
    width: 5rem;
    box-shadow: rgba(172, 172, 180, 0.2) 0 2px 2px, rgba(172, 172, 180, 0.2) 0 4px 4px, rgba(172, 172, 180, 0.2) 0 8px 8px, rgba(172, 172, 180, 0.2) 0 16px 16px, rgba(172, 172, 180, 0.2) 0 32px 32px, rgba(172, 172, 180, 0.2) 0 64px 64px;
    font-size: 0.9rem;

    .fa {
        display: block;
        padding-bottom: 5px;
        font-size: 1rem;
    }

    &:hover {
        cursor: pointer;
        box-shadow: rgba(0, 0, 0, 0.2) 0 2px 2px, rgba(255, 255, 255, 0.2) 0 4px 4px, rgba(0, 0, 0, 0.08) 0 8px 8px, rgba(3, 3, 3, 0.13) 0 16px 16px, rgba(182, 182, 182, 0.14) 0 32px 32px, rgba(0, 0, 0, 0.2) 0 64px 64px;
    }
}

.hidden,
.subnav_hidden {
    display: none;
}

.center {
    text-align: center;
}
@media (max-width: 830px) {
    .subnav {
        transform: translate3d(0, -100%, 0);
    }

    .subnav_hidden {
        display: block;
    }
}

.show {
    display: block !important;
}

a {
    color: black;
}

.repo_link {
    margin-bottom: 40px;
    a{
    position: relative;
    &::after {
        content: '';
        height: 2px;
        background: #4199e7;
        width: 100%;
        position: absolute;
        top: 30px;
        left: 0;
    }
}
}
